import Link from 'next/link';
import { Button } from '@/components/ui/Button';

export const Header = () => {
  const navLinks = [
    { name: 'Problema', href: '#problema' },
    { name: 'Solución', href: '#solucion' },
    { name: 'Impacto', href: '#impacto' },
    { name: 'Contacto', href: '#contacto' },
  ];

  return (
    <header className="sticky top-0 z-50 w-full border-b border-neutral bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
      <div className="container flex h-16 items-center justify-between">
        <Link href="/" className="flex items-center">
          {/* Logo de texto con el mismo estilo que el Home */}
          <span className="text-2xl font-bold tracking-tighter text-transparent bg-clip-text bg-gradient-to-r from-primary to-accent">
            IncluyeMe
          </span>
        </Link>
        
        <nav className="hidden md:flex items-center gap-6">
          {navLinks.map((link) => (
            <Link 
              key={link.name} 
              href={link.href} 
              className="text-sm font-medium text-secondary transition-colors hover:text-primary"
            >
              {link.name}
            </Link>
          ))}
        </nav>
        
        <div className="flex items-center gap-4">
          <Button href="#donar" variant="primary" className="hidden sm:flex !bg-accent hover:!bg-accent/90">
            Donar Ahora
          </Button>
        </div>
      </div>
    </header>
  );
};